:root {
  --main-color: #00acdc;
  --text-accent-color: #060b35;
  --text-secondary-color: #616070;
  --card-width: 200px;
  --gap-horizontal: 46px;
  --gap-vertical: 48px;
  --container-padding-horizontal: 40px;
  --container-padding-vertical: 50px;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 10px;
  line-height: 14px;
}

body {
  margin: 0;
}

.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: var(--text-accent-color);
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  margin-bottom: 16px;
}

.card__review-container {
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 24px;
  display: flex;
}

.card__review-count {
  color: var(--text-accent-color);
}

.card__price-container {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__price-label {
  font-size: 12px;
  line-height: 18px;
}

.card__price {
  color: var(--text-accent-color);
  font-size: 16px;
  font-weight: bold;
  line-height: 18px;
}

.card__buy {
  box-sizing: border-box;
  border: 1px solid var(--main-color);
  color: #fff;
  text-transform: uppercase;
  background-color: var(--main-color);
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__buy:hover {
  color: var(--main-color);
  background-color: #fff;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header .logo {
  width: 40px;
  height: 40px;
  display: block;
}

.header .logo__image {
  width: 100%;
  height: 100%;
}

.header .nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header .nav__list .nav__item {
  margin-left: 20px;
}

.header .nav__list .nav__item:first-child {
  margin-left: 0;
}

.header .nav__list .nav__link {
  white-space: nowrap;
  text-transform: uppercase;
  color: var(--text-accent-color);
  align-items: center;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.header .nav__list .nav__link:hover, .header .nav__list .nav__link.is-active {
  color: var(--main-color);
}

.header .nav__list .nav__link.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.cards {
  box-sizing: border-box;
  flex-wrap: wrap;
  width: 1024px;
  margin: 0 auto;
  padding: 50px 40px;
  display: flex;
}

.cards__card {
  margin-bottom: 46px;
  margin-right: 48px;
}

.cards__card:nth-child(4n) {
  margin-right: 0;
}

.cards__card:nth-last-child(-n+4) {
  margin-bottom: 0;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
